import React, {Component} from 'react';
import {Layer, Rect, Stage, Group, Circle, Arrow, Text, Path} from 'react-konva';
import SVG from '../img/svgPathHolder';

const sidemenuOffset = 219;
const modelItemOffset = 50;

class Canvas extends Component {
    constructor() {
        super();
        this.state = {
            isBinding: false
        }
    }

    onBind(e, id) {
        e.preventDefault();

        const newState = this.state;
        if (this.state.isBinding && id !== this.state.bindStartID) {
            newState.isBinding = false;
            newState.bindEndID = id;
            this.props.createConnectionBetween(this.state.bindStartID, newState.bindEndID);
        }
        this.setState(newState);
    }

    onStartBind(e, id) {
        e.preventDefault();

        const newState = this.state;
        if (this.state.isBinding) {
            newState.isBinding = false;
            newState.bindEndID = id;
            this.props.createConnectionBetween(this.state.bindStartID, newState.bindEndID);
        } else {
            newState.isBinding = true;
            newState.bindStartID = id;
        }
        this.setState(newState);
    }


    handleDrag(event, id) {
        const data = {
            x: event.target.attrs.x,
            y: event.target.attrs.y,
        };
        // if (data.x >= 0 && data.y >= 0){
        this.props.handleDrag({
            id, data
        });
        // }else{
        //     return false;
        // }
    }

    startEditing() {
        const newState = this.state;
        newState.isEditing = true;
        this.setState(newState);
    }

    stopEditing() {
        const newState = this.state;
        newState.isEditing = false;
        this.setState(newState);
    }

    handleNameChange() {
        this.stopEditing();
    }

    render() {

        const model = Object.keys(this.props.model).map(id => {
            const item = this.props.model[id];

            switch (item.type) {
                case 'connection':
                    const fromX = item.fromElement ? this.props.model[item.fromElement.id].x + this.props.model[item.fromElement.id][item.fromElement.side].x : item.from.x;
                    const fromY = item.fromElement ? this.props.model[item.fromElement.id].y + this.props.model[item.fromElement.id][item.fromElement.side].y : item.from.y;
                    const toX = item.toElement ? this.props.model[item.toElement.id].x + this.props.model[item.toElement.id][item.toElement.side].x : item.to.x;
                    const toY = item.toElement ? this.props.model[item.toElement.id].y + this.props.model[item.toElement.id][item.toElement.side].y : item.to.y;
                    return (
                        <Group key={id + 'group'}>
                            <Arrow
                                key={id + 'arrow'}
                                points={[fromX, fromY, toX, toY]}
                                pointerLength={15}
                                pointerWidth={10}
                                fill='black'
                                stroke='black'
                                strokeWidth={4}
                            />
                            <Rect
                                x={(fromX+toX)/2-33}
                                y={(fromY+toY)/2-15}
                                width={100}
                                height={30}
                                fill={'white'}
                                stroke="black"
                                strokeWidth={2}
                            />
                            <Text
                                x={(fromX+toX)/2-30}
                                y={(fromY+toY)/2-15}
                                text='HTTPS'
                                fontSize={30}
                                fontFamily='Calibri'
                                fill='black'
                            />
                            <Circle
                                x={fromX}
                                y={fromY}
                                radius={6}
                                stroke="black"
                                strokeWidth={2}
                                fill={'green'}
                                draggable="true"
                                onDragMove={(event) => this.props.handleConectionDrag({
                                    id,
                                    x: event.target.attrs.x,
                                    y: event.target.attrs.y,
                                    type: 'from'
                                })}
                                onDragEnd={(event) => this.props.handleConectionDrop({
                                    id,
                                    x: event.target.attrs.x,
                                    y: event.target.attrs.y,
                                    type: 'from'
                                })}
                            />
                            <Circle
                                x={toX}
                                y={toY}
                                radius={6}
                                stroke="black"
                                strokeWidth={2}
                                fill={'green'}
                                draggable="true"
                                onDragMove={(event) => this.props.handleConectionDrag({
                                    id,
                                    x: event.target.attrs.x,
                                    y: event.target.attrs.y,
                                    type: 'to'
                                })}
                                onDragEnd={(event) => this.props.handleConectionDrop({
                                    id,
                                    x: event.target.attrs.x,
                                    y: event.target.attrs.y,
                                    type: 'to'
                                })}
                            />

                        </Group>
                    );
                    break;
                case 'model':
                default:
                    return (
                        <Group
                            x={item.x}
                            y={item.y}
                            key={id + 'group'}
                            draggable="true"
                            onDragMove={(event) => this.handleDrag(event, id)}
                        >
                            <Rect
                                x={item.top.x}
                                y={item.top.y}
                                width={10}
                                height={10}
                                fill={'blue'}
                                stroke={item.top.isMarked ? "red" : "white"}
                            />
                            <Rect
                                x={item.right.x}
                                y={item.right.y}
                                width={10}
                                height={10}
                                fill={'blue'}
                                stroke={item.right.isMarked ? "red" : "white"}
                            />
                            <Rect
                                x={item.down.x}
                                y={item.down.y}
                                width={10}
                                height={10}
                                fill={'blue'}
                                stroke={item.down.isMarked ? "red" : "white"}
                            />
                            <Rect
                                x={item.left.x}
                                y={item.left.y}
                                width={10}
                                height={10}
                                fill={'blue'}
                                stroke={item.left.isMarked ? "red" : "white"}
                            />
                            <Circle
                                key={id}
                                x={0}
                                y={0}
                                radius={50}
                                stroke="#6F7698"
                                strokeWidth={2}
                                fill={'#C7CCEA'}
                            />
                            <Path
                                key={id+'path'}
                                x={-40}
                                y={-36}
                                data={SVG[item.subType]}
                                fill="black"
                                scale={{
                                    x:4,
                                    y:4
                                }}
                            />
                        </Group>
                    );
                    break;
            }
        });
        return (
            <div className="canvas-container">
                <div className="model-container">
                    <Stage width={700} height={700}>
                        <Layer>
                            {model}
                        </Layer>
                    </Stage>
                </div>
            </div>
        );
    }
}

export default Canvas;


