import React, {Component} from 'react';
import {Button} from 'react-bootstrap';
import SVG from '../img/svgPathHolder';


class Sidebar extends Component {
    render() {
        return (
            <div className="sidebar-wrapper">
                <div className="sidebar">
                    <input name="externalModel" id="externalModel" type="file"/>
                    <div className="sidebar-control-buttons">
                        <Button
                            bsStyle="primary"
                            onClick={(e) => this.props.onImport()}>
                            Import
                        </Button><Button
                            bsStyle="primary"
                            onClick={(e) => this.props.onSave()}>
                            Save
                        </Button>
                    </div>
                    <ul className="elements-ul-container">
                        <li>
                            <svg
                                onClick={() => this.props.addElement({type: 'model', subType:'desktop'})}>
                                <g className="scaleSVG">
                                    <path d={SVG.desktop}/>
                                </g>
                            </svg>
                        </li>
                        <li>
                            <svg
                                onClick={() => this.props.addElement({type: 'model', subType:'mobile'})}>
                                <g className="scaleSVG">
                                    <path d={SVG.mobile}/>
                                </g>
                            </svg>
                        </li>
                        <li>
                            <svg
                                onClick={() => this.props.addElement({type: 'model', subType:'camera'})}>
                                <g className="scaleSVG">
                                    <path d={SVG.camera}/>
                                </g>
                            </svg>
                        </li>
                        <li>
                            <svg
                                onClick={() => this.props.addElement({type: 'connection'})}>
                                <g className="scaleSVG">
                                    <path d={SVG.connection}/>
                                </g>
                            </svg>
                        </li>
                    </ul>
                    <div>
                        <ul>
                            <h3>Instructions</h3>
                            <li>
                                <h4>To create element</h4>
                                <p>Tap on side menu element</p>
                            </li>
                            <li>
                                <h4>To create connection</h4>
                                <p>Tap 'link' element to create element to link items</p>
                            </li>
                            <li>
                                <h4>To save model</h4>
                                <p>Tap "Save" button</p>
                            </li>
                            <li>
                                <h4>To load model</h4>
                                <p>Tap "Select file" button and chose file then tap on "Import" button to import model</p>
                            </li>
                        </ul>

                    </div>
                </div>
            </div>
        );
    }
}

export default Sidebar;


