class App extends Component {
    constructor() {
        super();

        this.state = {
            model: {}
        }
    }

    addElement(data) {
        const newState = this.state;
        const newID = new Date().getTime().toString();
        const connections = [];
        if (data.type === 'connection') {
            newState.model[newID] = {
                from: {
                    x: 20,
                    y: 20
                },
                to: {
                    x: 200,
                    y: 20
                },
                type: data.type
            };
        } else {
            newState.model[newID] = {
                x: 50,
                y: 50,
                top: {
                    x: -5,
                    y: -60,
                    isMarked: false
                },
                right: {
                    x: 50,
                    y: -5,
                    isMarked: false
                },
                down: {
                    x: -5,
                    y: 50,
                    isMarked: false
                },
                left: {
                    x: -60,
                    y: -5,
                    isMarked: false
                },
                connections: connections,
                type: data.type,
                subType: data.subType,
            };
        }
        this.setState(newState);
    }

    createConnectionBetween(aID, bID) {
        if (aID !== bID) {
            const newState = this.state;
            if (newState.model[aID].connections.indexOf(bID) === -1) {
                newState.model[aID].connections.push(bID);
            }
            this.setState(newState);
        }
    }

    handleDrag(event) {
        const newState = this.state;

        newState.model[event.id].x = event.data.x;
        newState.model[event.id].y = event.data.y;

        this.setState(newState);
    }

    onSave() {
        const filename = 'fi.json';
        const blob = new Blob([JSON.stringify(this.state.model)], {type: 'text/json'}),
            e = document.createEvent('MouseEvents'),
            a = document.createElement('a');

        a.download = filename;
        a.href = window.URL.createObjectURL(blob);
        a.dataset.downloadurl = ['text/json', a.download, a.href].join(':');
        e.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
        a.dispatchEvent(e)
    }

    onImport() {
        const files = document.getElementById('externalModel').files;
        console.log(files);
        if (files.length <= 0) {
            return false;
        }

        const fr = new FileReader();

        fr.onload = (e) => {
            console.log(e);
            var result = JSON.parse(e.target.result);
            const newState = this.state;
            newState.model = result;
            this.setState(newState);
        };

        fr.readAsText(files.item(0));
    }

    onNameChange(id, name) {
        const newState = this.state;
        newState.model[id].name = name;
        this.setState(newState);
    }

    handleConectionDrag(args) {
        const newState = this.state;
        let destination;
        switch (args.type) {
            case 'from':
                destination = 'fromElement';
                newState.model[args.id].from.x = args.x;
                newState.model[args.id].from.y = args.y;
                break;
            case 'to':
                destination = 'toElement';
                newState.model[args.id].to.x = args.x;
                newState.model[args.id].to.y = args.y;
                break;
            default:
                break;
        }
        newState.model[args.id][destination] = undefined;
        Object.keys(newState.model).map(id => {
            const item = newState.model[id];
            if (item.type === 'model') {
                if (this.distance(args, item) < 70) {
                    if (this.distance(args, {x: item.x + item.top.x, y: item.y + item.top.y}) < 30) {
                        item.top.isMarked = true;
                    } else {
                        item.top.isMarked = false;
                    }
                    if (this.distance(args, {x: item.x + item.right.x, y: item.y + item.right.y}) < 30) {
                        item.right.isMarked = true;
                    } else {
                        item.right.isMarked = false;
                    }
                    if (this.distance(args, {x: item.x + item.down.x, y: item.y + item.down.y}) < 30) {
                        item.down.isMarked = true;
                    } else {
                        item.down.isMarked = false;
                    }
                    if (this.distance(args, {x: item.x + item.left.x, y: item.y + item.left.y}) < 30) {
                        item.left.isMarked = true;
                    } else {
                        item.left.isMarked = false;
                    }
                }else{
                    item.top.isMarked = false;
                    item.right.isMarked = false;
                    item.left.isMarked = false;
                    item.down.isMarked = false;
                }
            }
        });
        this.setState(newState);
    }

    handleConectionDrop(args) {
        const newState = this.state;
        let destination;
        switch (args.type) {
            case 'from':
                destination = 'fromElement';
                break;
            case 'to':
                destination = 'toElement';
                break;
            default:
                console.error('handleConectionDrop:error');
                break;
        }
        Object.keys(newState.model).map(id => {
            const item = newState.model[id];
            const element = {};
            if (item.type === 'model') {
                if (this.distance(args, item) < 70) {
                    if (this.distance(args, {x: item.x + item.top.x, y: item.y + item.top.y}) < 30) {
                        newState.model[args.id][destination] = {};
                        newState.model[args.id][destination].side = 'top';
                        newState.model[args.id][destination].id = id
                    } else if (this.distance(args, {x: item.x + item.right.x, y: item.y + item.right.y}) < 30) {
                        newState.model[args.id][destination] = {};
                        newState.model[args.id][destination].side = 'right';
                        newState.model[args.id][destination].id = id
                    } else if (this.distance(args, {x: item.x + item.down.x, y: item.y + item.down.y}) < 30) {
                        newState.model[args.id][destination] = {};
                        newState.model[args.id][destination].side = 'down';
                        newState.model[args.id][destination].id = id
                    } else if (this.distance(args, {x: item.x + item.left.x, y: item.y + item.left.y}) < 30) {
                        newState.model[args.id][destination] = {};
                        newState.model[args.id][destination].side = 'left';
                        newState.model[args.id][destination].id = id
                    } else {
                        newState.model[args.id][destination] = undefined;
                    }
                }
            }
        });
        this.setState(newState);
    }

    distance(a, b) {
        return Math.sqrt(Math.pow((b.x - a.x), 2) + Math.pow((b.y - a.y), 2));
    }

    render() {
        return (
            <div className="app">
                <Sidebar
                    addElement={(data) => this.addElement(data)}
                    onSave={() => this.onSave()}
                    onImport={() => this.onImport()}
                />
                <Canvas
                    model={this.state.model}
                    handleDrag={(data) => this.handleDrag(data)}
                    createConnectionBetween={(a, b) => this.createConnectionBetween(a, b)}
                    onNameChange={(id, name) => this.onNameChange(id, name)}
                    handleConectionDrag={(args) => this.handleConectionDrag(args)}
                    handleConectionDrop={(args) => this.handleConectionDrop(args)}
                />
            </div>
        );
    }
}

export default App;


